"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"

export default function Home() {
  const [entry, setEntry] = useState("")
  const [mood, setMood] = useState("happy")
  const [entries, setEntries] = useState<any[]>([])
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState("")

  useEffect(() => {
    fetchEntries()
  }, [])

  const fetchEntries = async () => {
    try {
      const response = await fetch("/api/journal")
      const data = await response.json()
      if (response.ok) {
        setEntries(data)
      }
    } catch (err) {
      console.error("Failed to fetch entries:", err)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!entry.trim()) {
      setError("Please write something in your journal entry")
      return
    }
    
    setIsSubmitting(true)
    setError("")

    try {
      const response = await fetch("/api/journal", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          text: entry.trim(),
          mood,
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || "Failed to save entry")
      }

      setEntries([data, ...entries])
      setEntry("")
      setMood("happy")
    } catch (err: any) {
      setError(err.message)
    } finally {
      setIsSubmitting(false)
    }
  }

  const getMoodEmoji = (mood: string) => {
    const emojis: { [key: string]: string } = {
      happy: "üòä",
      sad: "üò¢",
      angry: "üò†",
      anxious: "üò∞",
      neutral: "üòê"
    }
    return emojis[mood] || "üòê"
  }

  const getMoodColor = (mood: string) => {
    const colors: { [key: string]: string } = {
      happy: "bg-green-500/10 text-green-400 border-green-500/50",
      sad: "bg-blue-500/10 text-blue-400 border-blue-500/50",
      angry: "bg-red-500/10 text-red-400 border-red-500/50",
      anxious: "bg-yellow-500/10 text-yellow-400 border-yellow-500/50",
      neutral: "bg-gray-500/10 text-gray-400 border-gray-500/50"
    }
    return colors[mood] || colors.neutral
  }

  return (
    <main className="container mx-auto px-4 py-8 min-h-screen">
      <div className="text-center mb-12 animate-fade-in">
        <h1 className="text-6xl font-bold mb-4 text-purple-500 font-marker animate-pulse">
          EmO MoNgRaL AI
        </h1>
        <p className="text-gray-400 text-xl">
          Your Personal Emotional Support Journal
        </p>
      </div>

      {error && (
        <div className="bg-red-500/10 border border-red-500 text-red-500 p-4 rounded-lg mb-4 animate-fade-in">
          {error}
        </div>
      )}

      <Card className="p-6 bg-zinc-800/50 border-purple-500 border-2 mb-8 transform hover:scale-[1.01] transition-all duration-300 backdrop-blur-sm">
        <form onSubmit={handleSubmit} className="space-y-4">
          <Textarea
            placeholder="How are you feeling today? Pour your heart out..."
            value={entry}
            onChange={(e) => setEntry(e.target.value)}
            className={cn(
              "min-h-[150px] bg-zinc-900/50 text-gray-100 border-purple-500/50",
              "focus:border-purple-500 focus:ring-purple-500/20",
              "placeholder:text-gray-500"
            )}
            style={{ resize: 'none' }}
          />
          <div className="flex gap-2 flex-wrap sm:flex-nowrap">
            <select
              value={mood}
              onChange={(e) => setMood(e.target.value)}
              className={cn(
                "bg-zinc-900/50 text-gray-100 border-purple-500/50 rounded-md p-2",
                "focus:border-purple-500 focus:ring-purple-500/20",
                "flex-grow sm:flex-grow-0"
              )}
            >
              <option value="happy">üòä Happy</option>
              <option value="sad">üò¢ Sad</option>
              <option value="angry">üò† Angry</option>
              <option value="anxious">üò∞ Anxious</option>
              <option value="neutral">üòê Neutral</option>
            </select>
            <Button 
              type="submit" 
              className={cn(
                "bg-purple-500 hover:bg-purple-600 transition-all duration-300",
                "flex-grow sm:flex-grow-0",
                "disabled:opacity-50 disabled:cursor-not-allowed"
              )}
              disabled={isSubmitting || !entry.trim()}
            >
              {isSubmitting ? "Sharing..." : "Share with EmO"}
            </Button>
          </div>
        </form>
      </Card>

      <div className="space-y-4">
        {entries.map((entry) => (
          <Card 
            key={entry.id} 
            className={cn(
              "p-6 bg-zinc-800/50 border-purple-500/50 border",
              "transform hover:scale-[1.02] transition-all duration-300",
              "hover:bg-zinc-800/80 backdrop-blur-sm"
            )}
          >
            <div className="flex justify-between items-start mb-4">
              <Badge 
                variant="outline" 
                className={cn(
                  "text-sm px-3 py-1",
                  getMoodColor(entry.mood)
                )}
              >
                {getMoodEmoji(entry.mood)} {entry.mood}
              </Badge>
              <span className="text-sm text-gray-500">
                {new Date(entry.timestamp).toLocaleDateString(undefined, {
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric',
                  hour: '2-digit',
                  minute: '2-digit'
                })}
              </span>
            </div>
            <p className="text-gray-300 mb-4 whitespace-pre-wrap">{entry.text}</p>
            {entry.suggestion && (
              <div className={cn(
                "bg-purple-500/10 p-4 rounded-md border border-purple-500/20",
                "transform hover:scale-[1.01] transition-all duration-300"
              )}>
                <p className="text-purple-400">
                  <span className="font-bold">EmO says: </span>
                  {entry.suggestion}
                </p>
              </div>
            )}
          </Card>
        ))}
      </div>
    </main>
  )
}
